<template>
  <v-container>
    <v-layout align-center justify-center>
      <v-responsive class="mx-auto col-12 col-sm-4">
        <v-card class="my-5 border rounded-3">
          <v-form @submit.prevent="forgotPassword" ref="form" v-model="valid">
            <v-card-title class="bgred text-white rounded-top">Forgot Password</v-card-title>
            <v-card-text>
              <p class="mt-2">A link to reset your password will be sent to your e-mail address.</p>
              <v-text-field type="email"
                            v-model="email"
                            placeholder="Enter Email Address"
                            :rules="emailRules"
                            @keyup="emailVerify"
                            :error-messages="emailVerifyMessage"
                            prepend-inner-icon="mdi-email">
              </v-text-field>
              <div v-if="message" class="alert alert-success">{{message}}</div>
              <div v-if="error"  class="alert alert-danger">{{error}}</div>
              <div class="mt-1">
                <v-btn type="submit" color="primary" :disabled="!valid">Send Reset Link</v-btn>
              </div>
            </v-card-text>
          </v-form>
        </v-card>
      </v-responsive>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "UserForgetpassword",
  data() {
    return {
      emailVerifyMessage: '',
      email: '',
      valid: false,
      message: '',
      error: '',
      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
    }
  },
  methods: {
    emailVerify() { // verify email
      if (/.+@.+/.test(this.email)) {
        axios.post('user/verify', {'email': this.email})
            .then((resp) => {
              if (resp.data.status == 200) {
                this.emailVerifyMessage = resp.data.message;
              } else {
                this.emailVerifyMessage = "";
              }
            });
      }
    },

    forgotPassword() {
      axios.post('forgetpassword', {email: this.email})
          .then(response => {
            console.log(response.data);
            if(response.data.status == 200){
              this.message = response.data.message;
            }
            // Show success message to the user
          })
          .catch(error => {
            console.log(error.response.data);
            this.error = error.response.data.message;

          });
      this.$refs.form.validate();
    }
  }
}
</script>

<style scoped>
.bgred {background-color: #fd0606;}
</style>